<template>
    <el-upload
        ref="upload"
        class="avatar-uploader"
        :disabled="!!uploadedUrl"
        :show-file-list="false"
        :on-success="handleSuccess"
        :on-error="handleError"
        :on-progress="handleRemove"
        :on-change="handleCheckedSize"
        :before-upload="handleUpload"
        v-bind="uploadInfo">
        <div
            v-if="uploadedUrl"
            :style="uploadStyle"
            :class="['avatar-wrapper', 'el-upload-list__item', focusing ? 'focusing' : '', 'el-upload-list--picture-card']"
            @focus="focusing = true"
            @blur="focusing = false"
            @click="focusing = false">
            <img
                :src="uploadedUrl"
                :style="avatarStyle">
            <span class="el-upload-list__item-actions">
                <span
                    class="el-upload-list__item-delete"
                    @click="removePic">
                    <i class="el-icon-delete"></i>
                </span>
            </span>
        </div>
        <div v-else>
            <i
                v-if="!fileprogress"
                :style="uploadStyle"
                class="el-icon-plus avatar-uploader-icon"></i>
            <i
                v-else
                :style="uploadStyle"
                class="el-icon-loading avatar-uploader-icon"></i>
        </div>

    </el-upload>
</template>

<script>
export default {
    name: 'SingleUpload',
    props: {
        upload: {
            type: Object,
            default: () => ({})
        },
        imgW: {
            type: Number,
            default: 178,
            desc: '图片显示组件的宽度'
        },
        imgH: {
            type: Number,
            default: 178,
            desc: '图片显示组件的高度'
        },
        imageUrl: {
            type: String
        },
        sizeLimit: {
            type: String,
            default: '2M',
            desc: '上传图片大小限制'
        }
    },
    data () {
        return {
            focusing: false,
            fileprogress: false,
            exceedSize: false,
            uploadedUrl: ''
        }
    },
    watch: {
        imageUrl: {
            handler (val) {
                this.uploadedUrl = val
            },
            immediate: true
        }
    },
    computed: {
        uploadStyle () {
            return {
                width: this.imgW + 'px',
                height: this.imgH + 'px',
                lineHeight: this.imgH + 'px'
            }
        },
        avatarStyle () {
            return {
                maxWidth: this.imgW + 'px',
                maxHeight: this.imgH + 'px'
            }
        },
        uploadInfo () {
            const uploadInfo = Object.assign({
                accept: 'image/gif, image/jpeg, image/jpg, image/png',
                name: 'multiFile'
            }, this.upload)
            return uploadInfo
        },
        maxSize () {
            // 返回sizeLimit整数
            const sizeLimit = this.sizeLimit
            let unit = 'M'
            let foundUnit = sizeLimit.match(/(M|m)(B|b)*/)
            if (!foundUnit) {
                foundUnit = sizeLimit.match(/(K|k)(B|b)*/)
                if (foundUnit) {
                    unit = 'K'
                } else {
                    // 传递参数有问题的时候
                    unit = ''
                    console.log('参数SizeLimit传递有错误，传递的单位非K或者M')
                }
            }
            // 如果传递的数据不正确，就返回默认的2M
            return foundUnit ? {
                unit,
                size: sizeLimit.substring(0, foundUnit.index).trim()
            } : {
                unit: 'M',
                size: 2
            }
        }
    },
    methods: {
        handleRemove (file, fileList) {
            this.fileprogress = true
        },
        handleSuccess (file) {
            this.fileprogress = false
            // TODO：由于后端业务错误返回200，通过code判断异常
            if (file.code !== 200) {
                this.$confirm(file.message, '提示信息').catch(() => { })
            } else {
                this.uploadedUrl = file.data.accessUrl
                // TODO: 这里可能需要回传到父组件中整个URL
                this.$emit('back-event', {
                    imageUrl: file.data.accessUrl
                })
            }
        },
        handleError () {
            this.$confirm('服务端异常', '提示信息').catch(() => { })
        },
        removePic () {
            this.$confirm('确定要删除?', '提示信息').then(() => {
                this.uploadedUrl = ''
                this.$emit('back-event', {
                    imageUrl: ''
                })
            }).catch(() => { })
        },
        handleCheckedSize (input, inputList) {
            // 判断是否符合要求
            if (this.maxSize.unit === 'K') {
                this.exceedSize = input.size / 1024 > this.maxSize.size
            } else {
                this.exceedSize = input.size / (1024 * 1024) > this.maxSize.size
            }
        },
        handleUpload (file) {
            if (this.exceedSize) {
                this.$message({
                    message: `建议不要超过${this.maxSize.size}${this.maxSize.unit}`,
                    type: 'warning'
                })
                return false
            }
            if (this.uploadInfo.accept.indexOf(file.type) <= -1) {
                this.$message.error('上传文件格式不正确!')
                return false
            }
        }
    }
}
</script>

